:root{--color-blue: #1B97FF;--color-black: #333333;--color-gray: #D8D8D8;--color-white: #ffffff}.appear{-webkit-animation-name:appearAnimation;animation-name:appearAnimation;-webkit-animation-duration:.5s;animation-duration:.5s;-webkit-animation-timing-function:ease-in-out;animation-timing-function:ease-in-out}@-webkit-keyframes appearAnimation{0%{opacity:0}100%{opacity:1}}@keyframes appearAnimation{0%{opacity:0}100%{opacity:1}}.appearFromRight{-webkit-animation-name:appearRightAnimation;animation-name:appearRightAnimation;-webkit-animation-duration:.5s;animation-duration:.5s;-webkit-animation-timing-function:ease-in-out;animation-timing-function:ease-in-out}@-webkit-keyframes appearRightAnimation{0%{opacity:0;transform:translateX(24px)}100%{opacity:1;transform:translateY(0)}}@keyframes appearRightAnimation{0%{opacity:0;transform:translateX(24px)}100%{opacity:1;transform:translateY(0)}}

